/**
* 案件公示组件
* @module 案件
* @desc 添加&修改
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                center
                top="50px"
                width="800px"
        >
            <el-form
                    :model="form"
                    :rules="rules"
                    class="demo-ruleForm alert_wrop"
                    label-width="150px"
                    ref="ruleForm"
                    size="medium"
                    status-icon
                    v-loading="loading"
            >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="案件名称：" prop="caseName">
                            <el-select
                                    :disabled="isshow"
                                    @change="casenameFun"
                                    class="selectwidth"
                                    placeholder="请选择案件名称"
                                    v-model="form.caseName"
                            >
                                <el-option
                                        :key="item.scId"
                                        :label="item.caseName"
                                        :value="item.scId"
                                        v-for="item in caseNameArr"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="当事人类型：" prop="partyType">
                            <el-select
                                    :disabled="true"
                                    class="selectwidth"
                                    placeholder="请选择当事人类型"
                                    v-model="form.partyType"
                            >
                                <el-option
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                        v-for="item in partyTypeArr"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="当事人：" v-if="form.partyType!=2">
                            <el-input :disabled="true" placeholder="请输入当事人姓名" v-model="form.party"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="企业或组织名称：" v-if="form.partyType==2">
                            <el-input :disabled="true" placeholder="请输入企业或组织名称" v-model="form.partyOrg"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人名称：" v-if="form.partyType==2">
                            <el-input :disabled="true" placeholder="请输入法人姓名" v-model="form.party"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="处罚决定书编号：">
                            <el-input :disabled="true" placeholder="请输入处罚决定书编号"
                                      v-model="form.pmDecisionCode"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="处罚事项编号：">
                            <el-input :disabled="true" placeholder="请输入处罚事项编号" v-model="form.pmId"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="处罚事项版本：" prop="pmVersion">
                            <el-input :disabled="true" placeholder="请输入处罚事项版本" v-model="form.pmVersion"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="处罚名称：">
                            <el-input :disabled="true" placeholder="请输入处罚名称" v-model="form.pmName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="处罚依据：">
                            <el-input
                                    :disabled="true"
                                    placeholder="请输入处罚依据"
                                    type="textarea"
                                    v-model="form.accordingRegulations"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="处罚结果：">
                            <el-input
                                    :disabled="true"
                                    placeholder="请输入处罚结果"
                                    type="textarea"
                                    v-model="form.decisionContent"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="决定书日期：">
                            <el-date-picker
                                    :disabled="true"
                                    placeholder="选择决定书日期"
                                    type="date"
                                    v-model="form.caseDecisionDate"
                            ></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="执法部门：">
                            <!-- <el-input placeholder="请输入执法部门名称" v-model="form.handlingDepartmentName" :disabled="true"></el-input> -->
                            <el-select
                                    :disabled="true"
                                    class="selectwidth"
                                    placeholder="执法部门名称"
                                    v-model="form.handlingDepartmentName"
                            >
                                <el-option
                                        :key="item.orgId"
                                        :label="item.orgName"
                                        :value="item.orgId"
                                        v-for="item in orgList"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="24">
                        <el-form-item label="其他信息：">
                            <el-input placeholder="请输入其他信息" type="textarea" v-model="form.remarks"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="公示日期 ：" prop="caseDecisionDate">
                            <el-date-picker placeholder="选择公示日期" type="date"
                                            v-model="form.publicityDate "></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="公示有效期：">
                            <el-input placeholder="请输入公示有效期" v-model="form.publicityTime"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        apiCasePublicityAdd,
        apiCasePublicityGet,
        apiCasePublicityupdate,
        apiSimpleCaseSearch
    } from "../../../api/case";
    import {formatDate} from "../../../until/wordBook";

    export default {
        name: "edit",
        props: {
            orgList: {
                type: Array,
                default: []
            },
            isshow: {
                type: Boolean,
                default: false
            },
            data: {
                type: Object,
                default: () => {
                    return {};
                }
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "normal"
            },
            type: {
                type: String,
                default: ""
            },
            editId: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                loading: true,
                caseNameArr: [],
                partyTypeArr: [
                    {
                        value: "1",
                        label: "自然人"
                    },
                    {
                        value: "2",
                        label: "法人/其他组织"
                    },
                    {
                        value: "3",
                        label: "个体户"
                    }
                ],
                form: {
                    caseName: ""
                },


                rules: {
                    party: [{required: true, message: "内容不能为空", trigger: "blur"}],
                    caseName: [
                        {required: true, message: "内容不能为空", trigger: "blur"}
                    ],
                    partyOrg: [
                        {required: true, message: "内容不能为空", trigger: "blur"}
                    ],
                    pmDecisionCode: [
                        {required: true, message: "内容不能为空", trigger: "blur"}
                    ],
                    pmId: [{required: true, message: "内容不能为空", trigger: "blur"}],
                    pmVersion: [
                        {required: true, message: "内容不能为空", trigger: "blur"}
                    ],
                    pmName: [{required: true, message: "内容不能为空", trigger: "blur"}],
                    accordingRegulations: [
                        {required: true, message: "内容不能为空", trigger: "blur"}
                    ],
                    decisionContent: [
                        {required: true, message: "内容不能为空", trigger: "blur"}
                    ],
                    // decisionContent: [
                    //     {required: true, message: '内容不能为空', trigger: 'blur'},
                    // ],
                    pmOrg: [{required: true, message: "内容不能为空", trigger: "blur"}],
                    partyType: [
                        {required: true, message: "请选择类型", trigger: "change"}
                    ],
                    caseDecisionDate: [
                        {required: true, message: "请选择日期", trigger: "blur"}
                    ]
                }
            };
        },
        methods: {
            //选择案件获取案件信息
            casenameFun(val) {
                this.caseNameArr.forEach(item => {
                    if (item.scId == val) {
                        this.form.party = item.party;
                        this.form.partyType = item.partyType == "" ? "1" : item.partyType;
                        this.form.pmDecisionCode = item.pmDecisionCode;
                        this.form.pmId = item.pmId;
                        this.form.pmVersion = item.pmVersion;
                        this.form.pmName = item.pmName;
                        this.form.accordingRegulations = item.accordingRegulations;
                        this.form.partyOrg = item.partyOrg;
                        this.form.decisionContent = item.decisionContent;
                        this.form.caseDecisionDate = formatDate(
                            item.caseDecisionDate,
                            "yyyy-MM-dd"
                        );
                        this.form.handlingDepartmentName = item.handlingDepartmentName;
                    }
                });
            },

            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //清除验证样式
            clearV() {
                this.$refs.ruleForm.clearValidate();
            },

            //提交
            submitForm(formName) {

                this.$refs[formName].validate(valid => {
                    if (valid) {
                        this.form.caseDecisionDate = Date.parse(this.form.caseDecisionDate); //转时间戳
                        this.form.publicityDate = Date.parse(this.form.publicityDate); //转时间戳
                        // this.form.publicityTime = Date.parse(this.form.publicityTime);//转时间戳
                        if (this.type == 2) {
                            //添加
                            this.loading = true;
                            apiCasePublicityAdd(this.form).then(res => {
                                this.$emit("getList");
                                this.$message({
                                    type: "success",
                                    message: "添加成功!"
                                });
                                this.loading = false;
                                this.closeDialog();
                            });
                        } else if (this.type == 1) {
                            //编辑
                            this.loading = true;
                            apiCasePublicityupdate(this.form).then(res => {
                                this.$emit("getList");
                                this.$message({
                                    type: "success",
                                    message: "修改成功!"
                                });
                                this.loading = false;
                                this.closeDialog();
                            });
                        }
                    } else {
                        this.$message({
                            type: "warning",
                            message: "请填写完整 !"
                        });
                        return false;
                    }
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                if (this.editId) {
                    // 编辑接口
                    apiCasePublicityGet(this.editId).then(res => {
                        this.form = res.data;
                        this.form.publicityDate =
                            this.form.publicityDate == ""
                                ? ""
                                : formatDate(Number(this.form.publicityDate), "yyyy-MM-dd") ==
                                "NaN-aN-aN"
                                ? ""
                                : formatDate(Number(this.form.publicityDate), "yyyy-MM-dd");
                        //  this.form.publicityTime = this.form.publicityTime == '' ? "" : (formatDate(Number(this.form.publicityTime), 'yyyy-MM-dd') == 'NaN-aN-aN' ? '' : formatDate(Number(this.form.publicityTime), 'yyyy-MM-dd'))
                        this.loading = false;
                    });
                } else {
                    this.loading = false;

                    this.form = {
                        caseName: "",
                        party: "",
                        partyType: "",
                        partyOrg: "",
                        pmName: "",
                        accordingRegulations: "",
                        decisionContent: "",
                        pmOrg: "",
                        caseDecisionDate: "",
                        publicityDate: "",
                        publicityTime: ""
                    };
                }
            },

            //简易案件列表
            getcaseName() {
                apiSimpleCaseSearch({pageSize: 20, pageNum: 1}).then(res => {
                    this.caseNameArr = res.data;
                });
            }
        },

        filters: {
            //时间戳转换
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd");
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.getcaseName();
                    this.initPage();
                    //清除表单验证
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            }
        },
        created() {
        },
        mounted() {
        }
    };
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .selectwidth {
        width: 100%;
    }
</style>


