/**
* 案件公示组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-form :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                @selection-change="handleSelectionChange"
                border
                size="mini"
                v-loading="loading"
        >
            <!-- <el-table-column sortable align="center" label="违法案件名称" prop="caseName">
                      <template slot-scope="scope" v-if='scope.row.caseDecisionDate'>
                          {{scope.row.caseName | caseName}}
                      </template>
            </el-table-column>-->
            <el-table-column align="center" label="当事人" prop="party" sortable width="120"/>
            <!-- <el-table-column sortable align="center" label="当事人类型" prop="partyType"/>
            <el-table-column sortable align="center" label="企业/组织名称" prop="partyOrg"/>-->
            <el-table-column align="center" label="处罚名称" prop="pmName" show-overflow-tooltip sortable/>
            <el-table-column
                    align="center"
                    label="处罚依据"
                    prop="accordingRegulations"
                    show-overflow-tooltip
                    sortable
            />
            <el-table-column
                    align="center"
                    label="处罚结果"
                    prop="decisionContent"
                    show-overflow-tooltip
                    sortable
            ></el-table-column>
            <el-table-column align="center" label="执法部门" prop="pmOrg" show-overflow-tooltip sortable>
                <template slot-scope="scope" v-if="scope.row.pmOrg">{{scope.row.pmOrg | pmOrg}}</template>
            </el-table-column>
            <el-table-column align="center" label="决定书日期" prop="caseDecisionDate" sortable width="120">
                <template
                        slot-scope="scope"
                        v-if="scope.row.caseDecisionDate"
                >{{scope.row.caseDecisionDate | formatDate}}
                </template>
            </el-table-column>

            <el-table-column align="center" label="操作" width="100">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-view" type="success">
                            <i></i>
                        </el-link>
                    </el-tooltip>

                    <el-tooltip class="item" content="删除" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="delateFunction(scope.row)"
                                class="el-icon-delete btn-link"
                                type="danger"
                                v-if="buttonCheck('delete')"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="page-css"
        />
        <!-- 编辑&添加弹框 -->
        <change-edit
                :data="rowdata"
                :editId="editObj.editId"
                :isshow="editObj.isshow"
                :orgList="orgList"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.type"
                @getList="getList"
                ref="child"
        />
        <!-- 查看 -->
        <see-content
                :casenameArr="caseNameArr"
                :editId="seeObj.editId"
                :show.sync="seeObj.show"
                :title="seeObj.title"
        ></see-content>
    </div>
</template>

<script>
    import {apiOrgSearch} from "../../../api/system";
    import {apiCasePublicitydelete, apiCasePublicitySearch, apiSimpleCaseSearch} from "../../../api/case";
    import LjPagination from '../../../components/LjPagination'
    import LjSearchForm from '../../../components/LjSearchForm'
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seeContent";

    let that;
    export default {
        name: "publicityPage",
        data() {
            return {
                orgList: [],
                loading: true,
                caseNameArr: [],
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    type: "",
                    isshow: false,
                    editId: null
                },
                //查看数据
                seeObj: {
                    show: false,
                    title: "",
                    editId: null
                },

                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                input: "",
                list: [],
                rowdata: {},
                idarr: []
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchForm,
            seeContent
        },

        methods: {
            //批量选择
            handleSelectionChange(valarr) {
                let arr = [];
                valarr.forEach(item => {
                    arr.push(item.pubId);
                });
                let arrstr = arr.join(",");
            },

            //查询+搜索
            getList() {
                this.loading = true;
                apiCasePublicitySearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },

            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = "案件公示查看";
                this.seeObj.editId = row.pubId;
            },

            //新增账号
            onAdd() {
                this.editObj.type = "2";
                this.editObj.title = "新增案件公示信息";
                this.editObj.show = true;
                this.editObj.editId = null;
                this.editObj.isshow = false;
            },

            //编辑
            editFunction(row) {
                this.editObj.type = "1";
                this.editObj.title = "修改案件公示";
                this.editObj.editId = row.pubId;
                this.editObj.show = true;
                this.editObj.isshow = true;
            },

            //删除按钮事件
            delateFunction(row) {
                this.$confirm("此操作将删除条数据, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    apiCasePublicitydelete(row.pubId).then(res => {
                        this.getList();
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                    });
                });
            },

            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                apiCasePublicitySearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },

        beforeCreate: function () {
            that = this;
        },
        filters: {
            //性别判断
            ifsex(val) {
                return IFSEX[val];
            },
            //时间戳
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd");
            },
            //案件名称
            caseName(val) {
                if (
                    that.caseNameArr.find(item => {
                        return item.scId == val;
                    })
                ) {
                    return that.caseNameArr.find(item => {
                        return item.scId == val;
                    }).caseName;
                }
            },
            //执法部门名称
            pmOrg(val) {
                if (
                    that.orgList.find(item => {
                        return item.orgId == val;
                    })
                ) {
                    return that.orgList.find(item => {
                        return item.orgId == val;
                    }).orgName;
                }
            }
        },
        computed: {},
        mounted() {
        },

        created() {
            this.getList();
            apiSimpleCaseSearch({pageSize: 20, pageNum: 1}).then(res => {
                this.caseNameArr = res.data;
            });
            //执法部门
            apiOrgSearch().then(res => {
                this.orgList = res.data;

            });
        },
    }
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px;
    }

    .btn-link {
        margin-left: 10px;
    }

    .page-css {
        margin-top: 10px;
    }
</style>
