/**
* 案件公示组件
* @module 案件
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog
            :close-on-click-modal="false"
            :title="title"
            :visible="show"
            @close="closeDialog"
            append-to-body
            top="50px"
            width="80%"
    >
        <div class="detail-block">
            <table class="gridTable" v-loading="loading">
                <!-- <tr>
                            <td  >案件名称：</td>
                            <td colspan="4">
                               <span class="fontcss" v-if="casenameArr.find(item=>{return item.scId == form.caseName})">{{casenameArr.find(item=>{return item.scId == form.caseName}).caseName}}</span>
                            </td>
                </tr>-->
                <tr v-if="form.partyType==2">
                    <td class="label">当事人类型：</td>
                    <td colspan="4">
            <span
                    class="fontcss"
                    v-if="partyTypeArr[form.partyType]"
            >{{partyTypeArr[form.partyType]}}</span>
                    </td>
                </tr>

                <tr v-if="form.partyType!=2">
                    <td class="label">当事人类型：</td>
                    <td>
            <span
                    class="fontcss"
                    v-if="partyTypeArr[form.partyType]"
            >{{partyTypeArr[form.partyType]}}</span>
                    </td>
                    <td class="label">当事人姓名：</td>
                    <td>
                        <span class="fontcss">{{form.party}}</span>
                    </td>
                </tr>
                <tr v-if="form.partyType==2">
                    <td class="label">企业/组织名称：</td>
                    <td>
                        <span class="fontcss">{{form.partyOrg}}</span>
                    </td>
                    <td class="label">法人姓名：</td>
                    <td>
                        <span class="fontcss">{{form.party}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">处罚决定书编号：</td>
                    <td colspan="4">
                        <span class="fontcss">{{form.pmDecisionCode}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">处罚名称：</td>
                    <td colspan="4">
                        <span class="fontcss">{{form.pmName}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">处罚依据：</td>
                    <td colspan="4">
                        <span class="fontcss">{{form.accordingRegulations}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">处罚结果：</td>
                    <td colspan="4">
                        <span class="fontcss">{{form.decisionContent}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">执法部门：</td>
                    <td colspan="4">
                        <span class="fontcss">{{form.pmOrg}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">其他信息：</td>
                    <td colspan="4">
                        <span class="fontcss">{{form.remarks}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">决定书日期：</td>
                    <td>
                        <span class="fontcss" v-if="form.caseDecisionDate">{{form.caseDecisionDate|formatDate}}</span>
                    </td>
                    <td class="label">公示日期：</td>
                    <td>
                        <span class="fontcss" v-if="form.publicityDate">{{form.publicityDate|formatDate}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">公示有效期：</td>
                    <td colspan="4">
                        <span class="fontcss" v-if="form.publicityTime">{{form.publicityTime}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">公示链接地址：</td>
                    <td colspan="4">
            <span class="fontcss">
              <a href="#">暂无</a>
            </span>
                    </td>
                </tr>
            </table>
        </div>
    </el-dialog>
</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";
    import {apiCasePublicityGet} from "../../../api/case";

    export default {
        name: "seeContent",
        props: {
            casenameArr: {
                type: Array,
                default: []
            },
            editId: {
                type: String,
                default: ""
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                loading: true,
                partyTypeArr: {
                    "1": "自然人",
                    "2": "法人/其他组织",
                    "3": "个体户"
                },
                form: {},
                enforceIdArr: [], //执法领域
                lawArr: [], //法律法规
                fineArr: [] //罚款
            };
        },
        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //弹框弹出时获取data给form
            initPage() {
                apiCasePublicityGet(this.editId).then(res => {
                    this.form = res.data;
                    this.loading = false;
                });
            }
        },
        filters: {
            //是否判断
            YESORNO(val) {
                return YESORNO[val];
            },
            //时间戳转换
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd");
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold;
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        // font-weight: 800;
        display: inline-block;
        min-width: 200px;
        border-radius: 5px;
        padding: 0 10px;
        // border-bottom: 1px dashed #C5C5C5
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
